/*Small devices, Tablets*/
@media(min-width: 768px) and (max-width:991px){

    /*Main Container*/
    .topContainer{
        .blueBuilding{
            width: 70%;
        }
    }
    .row.topContainer:after{   
        height: 300px;
        
    }
    .solutions{
        h1{
            font-size: 40px; 
            margin-bottom: 30px;
        }
        h3{
            font-size:20px;
        }
        .moreInformation{
            word-spacing:20px;
            margin-bottom: 50px;
        }
        .loremInfo{
            font-size: 16px;
        }
        .download{
            padding: 10px 50px;
        }
    }
    .pictures{
        .picDescription{
            min-height: 210px; 
        }
    }

    .componentTest{
        display: flex;
        // display: -webkit-flex;
        // flex-wrap: wrap;
        .architecture{
            overflow:hidden;
            height: 87%;
            width: 100%;
        }
        //can do calc to get like the left 
        //pic to match the right pic and nav bar 
        .building{
            height: 66%;
        }
        .glass{
            height: 21%;
        }
        .stantec{
            bottom: 118px;
        }
    }

    .free-trial{
        img{
            margin-left: 435px;
            width: 50%;
        }
        .free{
            padding-left: 60px;
        }   
    }

    .information{
        .customer{
            padding: 0 80px;
        }  
    }
    .row.topContainer:after{
        height: 300px;
    }
}

